import { useState } from 'react';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ReferenceLine,
  ResponsiveContainer,
} from 'recharts';

interface PlaceboData {
  x: number;
  density: number;
}

interface CityPlaceboConfig {
  name: string;
  actualEffect: number;
  data: PlaceboData[];
  xDomain: [number, number];
  yDomain: [number, number];
}

// Generate kernel density estimation data points
function generateKDE(
  mean: number,
  std: number,
  xMin: number,
  xMax: number,
  points: number = 100
): PlaceboData[] {
  const data: PlaceboData[] = [];
  const step = (xMax - xMin) / points;

  for (let x = xMin; x <= xMax; x += step) {
    // Gaussian kernel density
    const z = (x - mean) / std;
    const density = (1 / (std * Math.sqrt(2 * Math.PI))) * Math.exp(-0.5 * z * z);
    data.push({ x: Math.round(x * 100) / 100, density: Math.round(density * 1000) / 1000 });
  }

  return data;
}

// Placebo distributions based on the figure
const placeboConfigs: Record<string, CityPlaceboConfig> = {
  all: {
    name: 'All Cities',
    actualEffect: -1.268,
    data: generateKDE(0.1, 0.25, -2, 2, 120),
    xDomain: [-2, 2],
    yDomain: [0, 2],
  },
  beijing: {
    name: 'Beijing',
    actualEffect: -3.135,
    data: generateKDE(1.2, 0.8, -3.5, 3.5, 120),
    xDomain: [-3.5, 3.5],
    yDomain: [0, 1],
  },
  shanghai: {
    name: 'Shanghai',
    actualEffect: -1.131,
    data: generateKDE(0.3, 0.45, -2, 2, 120),
    xDomain: [-2, 2],
    yDomain: [0, 1.5],
  },
  guangzhou: {
    name: 'Guangzhou',
    actualEffect: -1.029,
    data: generateKDE(0.2, 0.5, -2, 2, 120),
    xDomain: [-2, 2],
    yDomain: [0, 1],
  },
};

type CityKey = keyof typeof placeboConfigs;

export default function PlaceboChart() {
  const [selectedCity, setSelectedCity] = useState<CityKey>('all');
  const config = placeboConfigs[selectedCity];

  return (
    <div className="placebo-chart-container">
      <div className="placebo-city-selector">
        {Object.entries(placeboConfigs).map(([key, cfg]) => (
          <button
            key={key}
            className={`placebo-city-btn ${selectedCity === key ? 'active' : ''}`}
            onClick={() => setSelectedCity(key as CityKey)}
          >
            {cfg.name}
          </button>
        ))}
      </div>

      <div className="placebo-chart">
        <ResponsiveContainer width="100%" height={300}>
          <AreaChart data={config.data} margin={{ top: 20, right: 30, left: 20, bottom: 20 }}>
            <CartesianGrid strokeDasharray="3 3" stroke="#e0e0e0" />
            <XAxis
              dataKey="x"
              type="number"
              domain={config.xDomain}
              tickFormatter={(v) => v.toFixed(1)}
              label={{
                value: 'Coefficients from placebo regression',
                position: 'insideBottom',
                offset: -10,
                style: { fontSize: '0.85rem', fill: '#64748b' },
              }}
            />
            <YAxis
              domain={config.yDomain}
              tickFormatter={(v) => v.toFixed(1)}
              label={{
                value: 'Density',
                angle: -90,
                position: 'insideLeft',
                style: { fontSize: '0.85rem', fill: '#64748b' },
              }}
            />
            <Tooltip
              formatter={(value) => [typeof value === 'number' ? value.toFixed(3) : value, 'Density']}
              labelFormatter={(label) => `Coefficient: ${typeof label === 'number' ? label.toFixed(2) : label}`}
              contentStyle={{
                backgroundColor: 'rgba(255, 255, 255, 0.95)',
                border: '1px solid #e2e8f0',
                borderRadius: '6px',
              }}
            />
            <Area
              type="monotone"
              dataKey="density"
              stroke="#3b82f6"
              fill="#93c5fd"
              fillOpacity={0.6}
              strokeWidth={2}
            />
            {/* Zero reference line (dashed) */}
            <ReferenceLine
              x={0}
              stroke="#000"
              strokeDasharray="5 5"
              strokeWidth={1}
            />
            {/* Actual DiD effect (solid red) */}
            <ReferenceLine
              x={config.actualEffect}
              stroke="#dc2626"
              strokeWidth={2}
              label={{
                value: `DiD = ${config.actualEffect}`,
                position: 'top',
                fill: '#dc2626',
                fontSize: 12,
                fontWeight: 600,
              }}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>

      <div className="placebo-interpretation">
        <p>
          <strong>Interpretation:</strong> The blue curve shows the distribution of 500 placebo
          coefficients (randomly assigned demolition years). The red line marks the actual DiD
          estimate ({config.actualEffect} K). The actual effect falls far in the left tail,
          confirming that the cooling effect is unlikely due to random chance.
        </p>
      </div>
    </div>
  );
}
